* {
  box-sizing: border-box;
}

.habit-header {
  display: flex;
  align-items: center;
  padding: 0 1em;
  background-color: blanchedalmond;
  font-size: 2.5rem;
}

.header-icon {
  color: green;
}

.header-title {
  margin: 0.5em 0.5em;
}

.habit-form {
  padding: 1em 3em;
}

.habit-input {
  font-size: 2.5rem;
  margin-right: 0.5em;
}

.habit-add-btn {
  padding: 1rem;
  font-size: 2rem;
  color: ivory;
  background-color: green;
}

button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.habit {
  font-size: 2.5rem;
  padding: 0.5em;
  list-style: none;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

.total {
  background-color: green;
  width: 3rem;
  height: 3rem;
}

.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
}

.habit-button:hover {
  opacity: 0.8;
}

.habit-increase,
.habit-decrease {
  color: goldenrod;
}

.habit-delete {
  font-size: 2rem;
  color: darkred;
}

.habits-reset {
  font-size: 1.5rem;
  padding: 0.2em 1em;
  background-color: green;
  color: white;
}